@import "https://fonts.googleapis.com/css?family=Lato:400,500,600,700&display=swap";


.wrapper {
	display: inline-flex;
	background: #fff;
	height: 100px;
	width: 400px;
	align-items: center;
	justify-content: space-evenly;
	border-radius: 5px;
	padding: 20px 15px;
	box-shadow: 5px 5px 30px rgba(0,0,0,0.2);
	.option {
		background: #fff;
		height: 100%;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: space-evenly;
		margin: 0 10px;
		border-radius: 5px;
		cursor: pointer;
		padding: 0 10px;
		border: 2px solid lightgrey;
		transition: all 0.3s ease;
		.dot {
			height: 20px;
			width: 20px;
			background: #d9d9d9;
			border-radius: 50%;
			position: relative;
			&::before {
				position: absolute;
				content: "";
				top: 4px;
				left: 4px;
				width: 12px;
				height: 12px;
				background: #0069d9;
				border-radius: 50%;
				opacity: 0;
				transform: scale(1.5);
				transition: all 0.3s ease;
			}
		}
		span {
			font-size: 20px;
			color: #808080;
		}
	}
}
input[type="radio"] {
	display: none;
}
#option-1 {
	&:checked {
		&:checked {
			~ {
				.option-1 {
					border-color: #0069d9;
					background: #0069d9;
					.dot {
						background: #fff;
						&::before {
							opacity: 1;
							transform: scale(1);
						}
					}
					span {
						color: #fff;
					}
				}
			}
		}
	}
}
#option-2 {
	&:checked {
		&:checked {
			~ {
				.option-2 {
					border-color: #0069d9;
					background: #0069d9;
					.dot {
						background: #fff;
						&::before {
							opacity: 1;
							transform: scale(1);
						}
					}
					span {
						color: #fff;
					}
				}
			}
		}
	}
}
